@mixin maxSmall {
  @media (max-width: 575px) {
    @content; } }
@mixin minSmall {
  @media (min-width: 576px) {
    @content; } }
@mixin maxMedium {
  @media (max-width: 767px) {
    @content; } }
@mixin minMedium {
  @media (min-width: 768px) {
    @content; } }
@mixin maxLarge {
  @media (max-width: 991px) {
    @content; } }
@mixin minLarge {
  @media (min-width: 992px) {
    @content; } }
@mixin maxXlarge {
  @media (max-width: 1199px) {
    @content; } }
@mixin minXlarge {
  @media (min-width: 1200px) {
    @content; } }
